import React from 'react';
import FoodBlock from './FoodBlock.jsx';
import '../styles/Food.css';
import '../styles/Headings.css';
import Shuffle from "../libs/shuffle.js"

const foodList = [

 [
  {name: '  /', price: 2, block: ''},
  {name: '  ', price: 2.5, block: ''},
  {name: ' c /  ', price: 2.5, block: ''},
  {name: ' c   ', price: 2.5, block: ''},
  {name: '  /  ', price: 2.2, block: ''},
  {name: '-  /', price: 2.2, block: ''},
  {name: ' ', price: 3, block: ''}
],

 [
  {name: ' ', price: 3, block: 'C'}, 
  {name: '   ', price: 3, block: 'C'}, 
  {name: '    ', price: 3, block: 'C'}, 
  {name: '     - ', price: 3, block: 'C'}
],

[
  {name: '  ', price: 2.5, block: ''}, 
  {name: '   ', price: 2.5, block: ''}, 
  {name: '   ', price: 2.5, block: ''}, 
  {name: '  /', price: 2.5, block: ''}, 
  {name: '   ', price: 2.5, block: ''},
],


[
  {name: ' ', price: 2.5, block: ''}, 
  {name: ' ', price: 2.5, block: ''}, 
  {name: ' ', price: 2.5, block: ''}, 
  {name: ' ', price: 2.5, block: ''}, 
],

[
  {name: '', price: 6, block: ''}, 
  {name: '30  ( )', price: 130, block: ''},
  {name: '  ', price: 6, block: ''}, 
  {name: '  ', price: 6, block: ''},
  {name: ' ', price: 6, block: ''}, 
  {name: '  ', price: 6, block: ''},  
  {name: ' ', price: 5, block: ''},
  {name: ' 4 ', price: 5, block: ''},
  {name: ' ', price: 4, block: ''},
  {name: '', price: 4, block: ''},
  {name: '', price: 1, block: ''}
],

 [
  {name: ' ', price: 2, block: ''},
  {name: '  ', price: 2.5, block: ''},
  {name: '', price: 3.8, block: ''},
  {name: '', price: 2, block: ''},
]
]

class Food extends React.Component {

  state = {
    plants: [1,2,3,4,5,6]
  }

componentWillMount() {
  this.setState({
    plants: Shuffle.make(this.state.plants)
})}

    render() {
      return    <div className="food">

      {foodList.map((item, i )=> (

          <FoodBlock key={`food_${i}`} styleClass={"foodblock--" + this.state.plants[i]} foodList={item}/>
        ))}


<div className="food__note foodblock">
<p>
  <span>*</span>     .
</p>
<p>1PRT = 0.001ETH</p> </div>
                
                    
                </div>
    
  }}
  
  export default Food;


