import React from 'react';
import Event from './Event.jsx';
import '../styles/Events.css';


const eventList = [
{day: '', time: '19:00', title: ' ', about: '<p>         .</p><p>    100500     .</p>'},
{day: '', time: '19:00', title: '', about: '<p>             .         .</p><p>      , <a href="mailto:hi@spb-frontend.ru"> </a>.</p>'},
{day: '', time: '9:30', title: '-', about: '<p>       ,   &nbsp;.</p><p>     ,   .</p>'},
{day: '', time: '19:00', title: 'Party hard', about: '<p> .</p> <p> ,   .</p><p> <a href="https://spb-frontend.ru/podcast"></a>.</p><p>   .</p>'}
]



class Events extends React.Component {

    constructor(props) {
      super(props);
      this.state ={
        party: false
      }
    }

    partyBgChange() {
      this.setState((prevSt) => ({
        party: !prevSt.party
      }))

    }

    render() {
      return    <div className={"events " + (this.state.party ? "events--party" : "")}>

        {eventList.map((item, i )=> (

          <Event key={`events_${i}`} when={item} handleBgParty={() => this.partyBgChange()}/>
        ))}
                </div>
    }
  }

  export default Events;



